body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 20px;
}

ol,
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

a {
    text-decoration: none;
    color: #333;
}

.App {
    width: 800px;
    margin: 0 auto;
}

.header {
    position: relative;
}

.header .menu {
    display: flex;
    position: absolute;
    top: 10px;
    right: 0;
}

.header .link {
    border: 1px solid #333;
    padding: 10px;
    margin-left: 10px;
    background-color: #efefef;
    font-weight: bold;
    border-radius: 4px;
}

.list_day {
    display: flex;
    flex-wrap: wrap;
}

.list_day li {
    flex: 20% 0;
    box-sizing: border-box;
    padding: 10px;
}

.list_day a,
.list_day button {
    display: block;
    width: 100%;
    padding: 20px 0;
    font-weight: bold;
    color: #fff;
    text-align: center;
    border-radius: 10px;
    background-color: dodgerblue;
}

table {
    border-collapse: collapse;
    width: 100%;
}
table td {
    width: 25%;
    height: 70px;
    border: 1px solid #ccc;
    text-align: center;
    font-size: 26px;
}

table td:first-child {
    width: 10%;
}

.off td {
    background: #eee;
    color: #ccc;
}

.btn_del {
    margin-left: 10px;
    color: #fff;
    background-color: firebrick;
}

button {
    padding: 10px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    border: 0 none;
    border-radius: 6px;
    padding: 10px 20px;
    color: #fff;
    background-color: dodgerblue;
}

.input_area {
    margin-bottom: 10px;
}

.input_area label {
    display: block;
    margin-bottom: 10px;
}

.input_area input {
    width: 400px;
    height: 40px;
    font-size: 20px;
    padding: 0 10px;
}

.input_area select {
    width: 400px;
    height: 40px;
    font-size: 20px;
}

.btn-delete {
    position: relative;
    padding: 20px 0;
    font-weight: bold;
    color: #fff;
    text-align: center;
    border-radius: 10px;
    background-color: dodgerblue;
}
.btn-delete input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.box {
    width: 100px;
    height: 100px;
    background-color: red;
}

